'use client';

import { useState } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { useAuth } from '@/hooks/use-auth';
import { 
  User, 
  Lock, 
  AlertCircle, 
  Eye, 
  EyeOff, 
  Building2,
  Shield,
  CheckCircle,
  Smartphone
} from 'lucide-react';

export function LoginForm() {
  const router = useRouter();
  const { login } = useAuth();
  const [formData, setFormData] = useState({
    username: '',
    password: ''
  });
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [isFocused, setIsFocused] = useState({
    username: false,
    password: false
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    setError(null);
  };

  const handleFocus = (field: string) => {
    setIsFocused(prev => ({ ...prev, [field]: true }));
  };

  const handleBlur = (field: string) => {
    setIsFocused(prev => ({ ...prev, [field]: false }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!formData.username || !formData.password) {
      setError('Por favor, complete todos los campos requeridos');
      return;
    }
    
    setIsLoading(true);
    const result = await login(formData);
    
    if (!result.success) {
      setError(result.error || 'Error de autenticaci√≥n. Verifique sus credenciales.');
      setIsLoading(false);
    } else {
      // Redirecci√≥n manejada por el hook de autenticaci√≥n
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-50 via-blue-50 to-slate-100 px-4 relative overflow-hidden">
      {/* Background Pattern */}
      <div className="absolute inset-0 bg-[linear-gradient(45deg,_#f8fafc_25%,_transparent_25%),_linear-gradient(-45deg,_#f8fafc_25%,_transparent_25%),_linear-gradient(45deg,_transparent_75%,_#f8fafc_75%),_linear-gradient(-45deg,_transparent_75%,_#f8fafc_75%)] bg-[size:20px_20px] [background-position:0_0,_0_10px,_10px_-10px,_-10px_0px] opacity-50"></div>
      
      {/* Security Badge */}
      <div className="absolute top-8 right-8 flex items-center gap-2 bg-white/80 backdrop-blur-sm px-4 py-2 rounded-full border border-green-200 shadow-sm">
        <Shield className="w-4 h-4 text-green-600" />
        <span className="text-sm font-medium text-green-700">Conexi√≥n Segura SSL</span>
      </div>

      <div className="w-full max-w-6xl flex flex-col lg:flex-row gap-8 items-center justify-center">
        {/* Left Side - Branding & Features */}
        <div className="w-full lg:w-1/2 max-w-lg space-y-8">
          {/* Company Logo */}
          <div className="flex items-center gap-4 mb-8">
            <div className="w-12 h-12 bg-gradient-to-br from-blue-600 to-blue-800 rounded-xl flex items-center justify-center shadow-lg">
              <Building2 className="w-6 h-6 text-white" />
            </div>
            <div>
              <h1 className="text-2xl font-bold text-slate-900">Enterprise<span className="text-blue-600">POS</span></h1>
              <p className="text-sm text-slate-600">Sistema de Gesti√≥n Comercial</p>
            </div>
          </div>

          <div className="space-y-6">
            <h2 className="text-4xl font-bold text-slate-900 leading-tight">
              Gesti√≥n Empresarial<br />
              <span className="text-blue-600">Inteligente 2025</span>
            </h2>
            
            <p className="text-lg text-slate-600 leading-relaxed">
              Acceda a la plataforma de gesti√≥n m√°s avanzada del mercado. 
              Control total de su negocio desde cualquier dispositivo.
            </p>

            {/* Features List */}
            <div className="space-y-4">
              {[
                'Dashboard anal√≠tico en tiempo real',
                'Gesti√≥n multi-sucursal integrada',
                'Reportes financieros automatizados',
                'Seguridad enterprise nivel bancario'
              ].map((feature, index) => (
                <div key={index} className="flex items-center gap-3">
                  <CheckCircle className="w-5 h-5 text-green-500 flex-shrink-0" />
                  <span className="text-slate-700 font-medium">{feature}</span>
                </div>
              ))}
            </div>

            {/* Stats */}
            <div className="flex gap-6 pt-4 border-t border-slate-200">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">15K+</div>
                <div className="text-sm text-slate-600">Empresas</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">99.9%</div>
                <div className="text-sm text-slate-600">Uptime</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">ISO</div>
                <div className="text-sm text-slate-600">27001 Certified</div>
              </div>
            </div>
          </div>
        </div>

        {/* Right Side - Login Form */}
        <Card className="w-full lg:w-1/2 max-w-md bg-white/80 backdrop-blur-sm border border-slate-200/80 shadow-xl rounded-2xl overflow-hidden">
          <CardHeader className="bg-gradient-to-r from-slate-900 to-blue-900 text-white p-8 pb-6">
            <div className="text-center space-y-2">
              <CardTitle className="text-2xl font-bold flex items-center justify-center gap-2">
                <Shield className="w-6 h-6" />
                Acceso Seguro
              </CardTitle>
              {/* Reemplazamos CardDescription por un p normal */}
              <p className="text-blue-100/80 text-sm">
                Ingrese sus credenciales corporativas
              </p>
            </div>
          </CardHeader>

          <CardContent className="p-8 space-y-6">
            <form onSubmit={handleSubmit} className="space-y-6">
              {error && (
                <div className="flex items-start gap-3 p-4 bg-red-50 border border-red-200 rounded-xl text-red-700 text-sm animate-fade-in">
                  <AlertCircle className="w-5 h-5 flex-shrink-0 mt-0.5" />
                  <div>
                    <div className="font-semibold">Error de Autenticaci√≥n</div>
                    <div className="mt-1">{error}</div>
                  </div>
                </div>
              )}

              {/* Username Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-slate-700 flex items-center gap-2">
                  <User className="w-4 h-4" />
                  Usuario Corporativo
                </label>
                <div className="relative">
                  <Input
                    type="text"
                    name="username"
                    value={formData.username}
                    onChange={handleChange}
                    onFocus={() => handleFocus('username')}
                    onBlur={() => handleBlur('username')}
                    placeholder="usuario@empresa.com"
                    className={`pl-4 pr-4 py-3 bg-white border-2 ${
                      isFocused.username 
                        ? 'border-blue-500 ring-2 ring-blue-500/20' 
                        : 'border-slate-200 hover:border-slate-300'
                    } text-slate-900 placeholder-slate-400 rounded-xl transition-all duration-200 font-medium`}
                    autoComplete="username"
                    autoFocus
                  />
                </div>
              </div>

              {/* Password Field */}
              <div className="space-y-2">
                <label className="text-sm font-medium text-slate-700 flex items-center gap-2">
                  <Lock className="w-4 h-4" />
                  Contrase√±a
                </label>
                <div className="relative">
                  <Input
                    type={showPassword ? "text" : "password"}
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    onFocus={() => handleFocus('password')}
                    onBlur={() => handleBlur('password')}
                    placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    className={`pl-4 pr-12 py-3 bg-white border-2 ${
                      isFocused.password 
                        ? 'border-blue-500 ring-2 ring-blue-500/20' 
                        : 'border-slate-200 hover:border-slate-300'
                    } text-slate-900 placeholder-slate-400 rounded-xl transition-all duration-200 font-medium`}
                    autoComplete="current-password"
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-slate-600 transition-colors duration-200 p-1 rounded-lg hover:bg-slate-100"
                  >
                    {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>

              {/* Options */}
              <div className="flex items-center justify-between text-sm">
                <label className="flex items-center gap-2 text-slate-600 cursor-pointer group">
                  <div className="relative">
                    <input 
                      type="checkbox" 
                      className="w-4 h-4 rounded border-slate-300 text-blue-600 focus:ring-blue-500 cursor-pointer" 
                    />
                  </div>
                  <span className="group-hover:text-slate-700 transition-colors">Mantener sesi√≥n</span>
                </label>
                <Link 
                  href="/forgot-password" 
                  className="text-blue-600 hover:text-blue-700 font-medium transition-colors"
                >
                  ¬øContrase√±a olvidada?
                </Link>
              </div>

              {/* Submit Button */}
              <Button
                type="submit"
                disabled={isLoading}
                className="w-full bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white font-semibold py-3.5 rounded-xl transition-all duration-300 shadow-lg hover:shadow-xl active:scale-[0.98] disabled:opacity-50 disabled:cursor-not-allowed group"
                size="lg"
              >
                {isLoading ? (
                  <div className="flex items-center gap-3">
                    <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                    <span>Verificando credenciales...</span>
                  </div>
                ) : (
                  <div className="flex items-center gap-3">
                    <Shield className="w-5 h-5 group-hover:scale-110 transition-transform" />
                    <span>Acceder al Sistema</span>
                  </div>
                )}
              </Button>

              {/* Divider */}
              <div className="relative py-4">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-slate-200"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-3 bg-white text-slate-500">Soporte T√©cnico</span>
                </div>
              </div>

              {/* Support Links */}
              <div className="text-center space-y-3">
                <div className="flex justify-center gap-6 text-sm">
                  <Link href="/support" className="text-slate-600 hover:text-slate-800 transition-colors font-medium">
                    Centro de Ayuda
                  </Link>
                  <Link href="/contact" className="text-slate-600 hover:text-slate-800 transition-colors font-medium">
                    Contactar Soporte
                  </Link>
                </div>
                <p className="text-xs text-slate-500">
                  ¬øProblemas de acceso? Contacte al departamento de TI<br />
                  <span className="font-medium">soporte@empresa.com | +1 (555) 123-4567</span>
                </p>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>

      {/* Footer */}
      <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 text-center">
        <div className="flex items-center gap-6 text-sm text-slate-500">
          <span>EnterprisePOS v4.0</span>
          <span>‚Ä¢</span>
          <span>¬© 2025 Enterprise Solutions Inc.</span>
          <span>‚Ä¢</span>
          <span className="flex items-center gap-1">
            <Smartphone className="w-4 h-4" />
            Disponible en dispositivos m√≥viles
          </span>
        </div>
        <div className="mt-2 flex justify-center gap-4 text-xs text-slate-400">
          <Link href="/privacy" className="hover:text-slate-600 transition-colors">Pol√≠tica de Privacidad</Link>
          <Link href="/terms" className="hover:text-slate-600 transition-colors">T√©rminos de Servicio</Link>
          <Link href="/compliance" className="hover:text-slate-600 transition-colors">Cumplimiento Normativo</Link>
        </div>
      </div>
    </div>
  );
}