'use client';

import { useEffect } from 'react';
import { User, LoginCredentials } from '../lib/types/usuario';
import { useAuthStore } from '@/store/auth-store';
import { Permission } from '@/lib/roles/role-types';
import { hasPermission as checkPermission } from '@/lib/roles/role-checker';
import { loginTestUser, debugUsers } from '@/lib/config/test-users';

export function useAuth() {
  const {
    user,
    isAuthenticated,
    isLoading,
    login: storeLogin,
    logout: storeLogout,
    initialize,
    clearLoading,
  } = useAuthStore();

  // Inicializar al montar el componente
  useEffect(() => {
    console.log('üéØ useAuth: Montando hook...');
    initialize();
    debugUsers();
  }, [initialize]);

  async function login(credentials: LoginCredentials) {
    console.log('üöÄ useAuth.login(): Iniciando proceso...', credentials);
    
    try {
      // 1. Validar credenciales con la funci√≥n de test
      const loggedUser = loginTestUser(credentials);
      
      if (!loggedUser) {
        console.error('‚ùå useAuth: Login fallido - Usuario no v√°lido');
        return { 
          success: false, 
          error: 'Usuario o contrase√±a incorrectos' 
        };
      }

      console.log('‚úÖ useAuth: Usuario v√°lido encontrado:', loggedUser.username);

      // 2. Preparar datos del usuario
      const updatedUser: User = {
        ...loggedUser,
        ultimo_acceso: new Date().toISOString(),
      };

      // 3. Generar tokens
      const token = `fake-token-${updatedUser.id}-${Date.now()}`;
      const refreshToken = `fake-refresh-${updatedUser.id}-${Date.now()}`;

      console.log('üîë useAuth: Tokens generados');

      // 4. Llamar al store para guardar el estado
      storeLogin(updatedUser, token, refreshToken);

      console.log('üéâ useAuth: Login completado exitosamente');
      return { 
        success: true, 
        user: updatedUser 
      };

    } catch (error) {
      console.error('üí• useAuth: Error durante login:', error);
      return { 
        success: false, 
        error: 'Error interno del sistema' 
      };
    }
  }

  function logout() {
    console.log('üö™ useAuth: Ejecutando logout...');
    storeLogout();
    // Limpiar cookie tambi√©n
    document.cookie = 'auth_token=; path=/; expires=Thu, 01 Jan 1970 00:00:00 GMT';
    console.log('‚úÖ useAuth: Logout completado');
  }

  function hasPermission(permission: Permission | string): boolean {
    if (!user) {
      return false;
    }
    return checkPermission(user, permission as Permission);
  }

  return { 
    user, 
    isAuthenticated, 
    isLoading, 
    login, 
    logout, 
    hasPermission 
  };
}